import React, { useState } from 'react';
import Swal from 'sweetalert2';
import "./ContactForm.css";

const Contact = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    msg: ''
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formBody = new FormData();
    formBody.append('name', formData.name);
    formBody.append('email', formData.email);
    formBody.append('subject', formData.subject);
    formBody.append('msg', formData.msg);
    formBody.append('_captcha', 'false'); // disable captcha if desired

    try {
      const response = await fetch(
        'https://formsubmit.co/ajax/hajji.youssef.contact@gmail.com',
        {
          method: 'POST',
          body: formBody,
          headers: { 'Accept': 'application/json' }
        }
      );

      const result = await response.json();
      if (result.success) {
        Swal.fire({
          title: 'Message Sent!',
          text: 'Your message was sent successfully.',
          icon: 'success'
        });
        setFormData({ name: '', email: '', subject: '', msg: '' }); // clear form
      } else {
        Swal.fire({
          title: 'Oops!',
          text: 'Something went wrong. Please try again.',
          icon: 'error'
        });
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: 'Failed to send message.',
        icon: 'error'
      });
    }
  };

  return (
    <div className="form-container">
      <h1>Contact Mhh</h1>
      <form onSubmit={handleSubmit}>
        <input type="text" name='name' placeholder="Name" value={formData.name} onChange={handleChange} />
        <input type="email" name='email' placeholder="Email" value={formData.email} onChange={handleChange} />
        <input type="text" name='subject' placeholder="Subject" value={formData.subject} onChange={handleChange} />
        <textarea name='msg' placeholder="Message" value={formData.msg} onChange={handleChange}></textarea>
        <button className='btn' type='submit'>Submit</button>
      </form>
    </div>
  );
};

export default Contact;
